import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/dist/ScrollTrigger.js";
import Scrollbar from "smooth-scrollbar";
import "./style.css";
gsap.registerPlugin(ScrollTrigger);

const MomentumParallax = class {
  constructor(elem, options) {
    let _thisobj = Scrollbar.init(elem,options);
    ScrollTrigger.scrollerProxy(options.proxyElem, {
      scrollTop(value) {
        if (arguments.length) {
          _thisobj.scrollTop = value;
        }
        return _thisobj.scrollTop;
      },
      getBoundingClientRect() {
        return {
          top: 0,
          left: 0,
          width: window.innerWidth,
          height: window.innerHeight,
        };
      },
    });
    
    _thisobj.addListener(ScrollTrigger.update);
    ScrollTrigger.addEventListener("refresh", function () {
      _thisobj.update();
    });

    this.scrollobj = _thisobj;


  }
};
export {
  MomentumParallax, gsap, ScrollTrigger,
}